.card {
  display: flex;
  flex-direction: column;
  place-items: center;
  background: $color-white;
  border: 1px solid $color-white-polar;
  padding: 32px 16px 16px;
  border-radius: $border-radius-main;
  width: $card-width;

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $color-gray-light;
    margin-bottom: 4px;
    transition: color 0.3s;
  }

  &:hover &__title {
    color: $color-blue;
  }

  &__product {
    width: $width-section;
    color: $color-gray;
    margin-bottom: 16px;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: $width-section;
    margin-bottom: 24px;
  }

  &__review-value {
    color: $color-gray-light;
  }

  &__price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    width: $width-section;
    line-height: 18px;
    margin-bottom: 16px;
  }

  &__price-text {
    font-size: 12px;
    color: $color-gray;
  }

  &__price-value {
    font-family: Roboto-bold, sans-serif;
    font-size: 16px;
    font-weight: 700;
    color: $color-gray-light;
  }

  &__button {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    color: $color-white;
    background: $color-blue;
    border: 1px solid $color-blue;
    border-radius: $border-radius-main;
    padding: 12px 70px;
    transition: 0.3s;
  }

  &__button:hover {
    color: $color-blue;
    background-color: $color-white;
    transform: scale(109%);
  }
}
